.grid {
  display: grid;
}

.sec1 {
  grid-column: 1/3;
  grid-row: 1/3;
}

.sec2 {
  grid-column: 3/4;
  grid-row: 1/7;
}

.sec3 {
  grid-column: 1/2;
  grid-row: 3/4;
}

.sec4 {
  grid-column: 2/3;
  grid-row: 3/4;
}

.sec5 {
  grid-column: 1/2;
  grid-row: 4/5;
}

.sec6 {
  grid-column: 2/3;
  grid-row: 4/7;
}

.sec7 {
  grid-column: 1/2;
  grid-row: 5/7;
}
